@import '../../styles/utils/mixins';

.itempage {
  padding-top: 24px;
  padding-bottom: 80px;

  @include on-tablet {
    padding-bottom: 64px;
  }

  &__bread-crumbs-wrapper {
    grid-column: 1/-1;
  }

  &__loader-wrapper {
    padding-top: 50px;
    grid-column: 1/-1;
    height: 80vh;
  }

  display: grid;
  justify-content: center;
  gap: 16px;
  grid-template-columns: repeat(24, 1fr);

  @include on-tablet {
    grid-template-columns: repeat(12, 1fr);
  }

  @include on-mobile {
    grid-template-columns: repeat(4, 1fr);
  }
}
